.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.content {
    background: #181a1a;
    padding: 20px;
    border-radius: 4px;
    width: 350px;
}

.content input {
    color: white;
    /* outline: 1.5px solid white;
    border-radius: 5px;
    z-index: 0; */
}

.content label {
    color: white;
}

