.overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}

.todoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.todoContainer h1 {
    font-size: 60px;
    font-weight: 300;
    margin: 40px 0;
}

.list {
    margin: 30px 0;
    width: 740px;
    border: 1px solid grey;
    height: 370px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 15px;
}

.item {
    border-bottom: 1px solid #f7e2df4f;
}

.description {
    width: 100%;
    height: 40px;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 17px;
    margin: 0 10px;
    text-align: start;
    display: flex;
    align-items: center;
}

.edit {
    border: 1px solid white;
    width: 100%;
    height: 40px;
    padding-left: 10px;
    color: white;
    background: transparent;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 17px;
}

.item:first-child .edit {
    border-top-left-radius: 14px;
}

.btns {
    width: 160px;
    background: #0000007c;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    gap: 4px;
    height: 40px;
}

.item:first-child .btns {
    border-top-right-radius: 14px;
}

.item:last-child .btns {
    border-bottom-right-radius: 14px;
}

.button {
    cursor: pointer;
}

.addBtn {
    color: black;
    background: #f7e2df;
    font-weight: 600;
    font-size: 15px;
    padding: 0 16px;
    cursor: pointer;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.interaction {
    display: flex;
    position: relative;
}

.addInput {
    width: 520px;
    background: #181a1a;
    color: white;
    font-size: 17px;
    padding: 10px 16px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.quantity {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    position: absolute;
    top: 45px;
    right: 95px;
}

button:disabled {
    opacity: 0.3;
}

.error {
    color: rgba(255, 0, 0);
}

.saveBtn {
    height: 100%;
    width: 100%;
    cursor: pointer;
    background: transparent;
    color: white;
    font-size: 15px;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pagination {
    background: rgb(255, 255, 255);
    padding: 10px 22px;
    border-radius: 40px;
    display: flex;
    align-items: center;
}

.pagination button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    color: black;
    font-size: 16px;
    font-weight: 800;
    margin: 0 7px;
    cursor: pointer;
}

.page li {
    display: block;
    margin: 0 3px;
    color: black;
    padding: 7px 12px;
    border-radius: 50%;
    font-weight: 700;
    cursor: pointer;
}

.page .activePage {
    background: #181a1a;
    color: white;
    cursor: pointer;
    font-weight: 700;
}

.sortWrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 40px;
    padding: 0 12px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    background: #0000007c;
    margin-top: auto;
    font-size: 17px;
}

.sortWrapper button {
    margin: 0 10px;
    background: transparent;
    color: white;
    font-size: 14px;
    font-weight: 300;
    cursor: pointer;
}

@media (max-width: 490px) {
    .todoContainer h1 {
        font-size: 45px;
    }
    .saveBtn {
        font-size: 13px;
    }
    .addInput {
        width: 270px;
        font-size: 14px;
    }
    .addBtn {
        font-size: 14px;
        padding: 0 12px;
    }
    .list {
        font-size: 13px;
        width: 360px;
    }
    .description,
    .edit {
        font-size: 13px;
    }
    .btns {
        width: 130px;
        font-size: 19px;
    }
    .description {
        overflow: scroll;
    }
    .sortWrapper {
        font-size: 12px;
        flex-direction: column-reverse;
        justify-content: center;
        height: 50px;
    }
    .sortWrapper button {
        font-size: 15px;
    }
    .quantity {
        font-size: 12px;
        right: 85px;
        top: 40px;
    }
}
